
import css from './Dropdown.module.css'

export const Dropdown=({ setSelectedButton })=>{

const handleClick=(e)=>{
    console.log(e.target.value);
    setSelectedButton(e.target.value)
}


    return (
        <div className={css.wrapper}>
          <button className={css.btn}
            value="show all"
            onClick={handleClick}
          >
            Show all
          </button>
           <button className={css.btn}
        value='follow'   onClick={handleClick}
          >
            Follow
          </button>
           <button className={css.btn}
            value='followings'   onClick={handleClick}
          >
            Followings
          </button>
        </div>
      );
}